import React from 'react'
import { Navbar, Container, Nav, NavDropdown } from 'react-bootstrap'
import { reports } from '../config/reports'
import HelpPDF from '../assets/CCF-CA.pdf'
import { usePowerBi } from '../context/PowerBiContext'
import Logo from '../assets/logo192.png'

export default function NavBar() {
    const { handleReport } = usePowerBi()

    return (
        <Navbar bg="primary" variant='dark' expand="md">
            <Container>
                <div className="logo">
                    <img src={Logo} alt="Logo" />
                </div>
                <div className="Title">
                    <h5>Development Management</h5>
                </div>

                <Navbar.Toggle aria-controls="navbar-nav" />
                <Navbar.Collapse id="navbar-nav">
                    <Nav className="me-auto">

                        {
                            Object.keys(reports).splice(1).map((key, index) => (
                                !Array.isArray(reports[key]))
                                ? (
                                    <Nav.Link
                                        onClick={() => handleReport(reports[key])}
                                        key={index}
                                    >
                                        {reports[key].label}
                                    </Nav.Link>
                                )
                                : (
                                    <NavDropdown key={index} title={key.split('_')[0]}>
                                        {
                                            reports[key].map((report, index) => (
                                                <NavDropdown.Item
                                                    onClick={() => handleReport(report)}
                                                    key={index}
                                                >
                                                    {report.label}
                                                </NavDropdown.Item>
                                            ))
                                        }
                                    </NavDropdown>
                                )
                            )
                        }

                        <Nav.Link href={HelpPDF} target="_blank">Help</Nav.Link>
                    </Nav>
                </Navbar.Collapse>
            </Container>
        </Navbar>
    )
}