import { useContext, useEffect, useState, createContext } from 'react'
import { config } from '../config'
import { reports } from '../config/reports'
import axios from 'axios'

const PowerBiContext = createContext(null)

export default function PowerBiProvider({ children }) {
    const [currentReport, setCurrentReport] = useState(Object.values(reports)[0])
    const [report, setReport] = useState(currentReport)
    const [loading, setLoading] = useState(true)

    useEffect(() => {
        const fetchAccessToken = async () => {
            try {
                const tokenUrl = currentReport?.tokenUrl || config.defaultTokenURL

                const { data } = await axios.get(tokenUrl)

                currentReport.accessToken = data?.EmbedToken
                currentReport.id = data?.ReportId

                setReport(currentReport)
                setLoading(false)

            } catch (error) {
                console.log(error)
            }

        }

        fetchAccessToken()
    }, [currentReport])


    const handleReport = (report) => {
        setCurrentReport(report)
    }


    return (
        <PowerBiContext.Provider value={{ report, loading, handleReport }}>
            {children}
        </PowerBiContext.Provider>
    )
}


export const usePowerBi = () => useContext(PowerBiContext)